.card {
  transition: transform 0.5s ease-in-out;
  cursor: pointer;
}

.card:hover {
  transform: scale(1.1);
}

.heading-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

h3 {
  font-size: 24px;
  font-weight: bold;
}

button {
  position: relative;
  background-color: #fbaf0b !important; /* Add your desired background color here */
  color: rgb(7, 5, 0);
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
