.card {
  transition: transform 0.5s ease-in-out;
  cursor: pointer;
  width: 50;
  height: 50;
}

.card:hover {
  transform: scale(1.1);
}

button {
  position: relative;
  background-color: #fbaf0b !important; /* Add your desired background color here */
  color: rgb(7, 5, 0);
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
h3 {
  font-size: 24px;
  font-weight: bold;
}
