/* RecipeGenerator.css */

.recipe-generator-container {
  background-image: url("imggds.jpg");
  background-size: cover;
  max-width: 80%;
  margin: 20px auto; /* Center the container horizontally */
  padding: 50px;
  border-radius: 30px;
}

form {
  margin-bottom: 20px;
}

input {
  width: 50%;
  padding: 8px;
  font-size: 16px; /* Adjust the font size as needed */
  margin-bottom: 10px;
  border-radius: 10px;
}

button {
  position: relative;
  background-color: #eda508 !important; /* Add your desired background color here */
  color: rgb(7, 5, 0);
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-inline-start: 10px;
}

.generated-recipe-container {
  padding-top: 20px; /* Adjust as needed */
}

/* Adjust the style for the generated recipe text */
.generated-recipe-ingredient {
  display: block; /* This will ensure each ingredient is on a new line */
  margin-bottom: 5px; /* Adjust spacing between ingredients */
  font-size: 14px; /* Adjust the font size as needed */
}
