.card-content {
  padding: 10px; /* Add padding to card content */
}

.delete-button {
  position: absolute; /* Position the button absolutely within the card */
  bottom: 10px; /* Set the button's bottom position */
  right: 10px; /* Set the button's right position */
  background-color: #f44336; /* Set the button's background color */
  color: white; /* Set the button's text color */
  border: none; /* Remove default button border */
  border-radius: 5px; /* Add rounded corners to the button */
  padding: 5px 10px; /* Add padding to the button */
  cursor: pointer; /* Make the button clickable */
}

.delete-button:hover {
  background-color: #c0392b; /* Change button background color on hover */
}

.card-image {
  width: 20%; /* Set image width to 100% of the card */
  height: 20%; /* Reduce image height */
  object-fit: cover; /* Resize image to fit the container */
}
